<template>
  <div class="main">
    <a-breadcrumb>
      <a-breadcrumb-item>首页</a-breadcrumb-item>
      <a-breadcrumb-item>企业信息</a-breadcrumb-item>
    </a-breadcrumb>
    <h2 class="title">企业信息</h2>
    <div class="search_box">
      <span>企业库ID：</span>
      <a-input placeholder="输入企业库ID" v-model="searchid" />
      <span>企业名称：</span>
      <a-input placeholder="输入企业名称" v-model="searchname" />
      <a-button type="primary" @click="searchSubmit">查询</a-button>
    </div>

    <div class="content_box">
      <div class="content_title">
        <h3>零部件分类列表</h3>
        <a-button type="primary" @click="showAddClassificationVisible()">添加</a-button>
        <!-- <a-button icon="download" @click="showModal5">批量导入</a-button> -->
      </div>
      <div class="content_table">
        <a-layout>
          <!-- :scroll="{ y: 300 }" -->
          <a-table
            :columns="columns"
            :dataSource="dataEnterprise"
            :pagination="showpagination"
            rowKey="businessId"
            :scroll="{ y: 300 }"
             style="height:calc(100vh -200px);"
             class="enterpriseinfo-table"
          >
            <template slot="key" slot-scope="text, record">
              <span class="lineEllipsis">{{text}}</span>
            </template>

            <template slot="title" slot-scope="text, record">
              <span class="lineEllipsis">{{text}}</span>
            </template>

            <template slot="action" slot-scope="text, record">
              <a
                href="javascript:;"
                class="no-underline"
                @click="showEditClassificationVisible(record)"
                style="text-decoration: none;color:#00aaee"
              >修改</a>

              <a-popconfirm
                title="确定删除这条数据吗?"
                @confirm="() => onDelete(record.businessId)"
                cancelText="取消"
                okText="确定"
              >
                <a href="javascript:;" class="no-underline" style="text-decoration: none;color:#00aaee">删除</a>
              </a-popconfirm>

            </template>
          </a-table>

          <div class="pagination-box">
            <a-pagination
              @change="onChangepagination"
              :pageSizeOptions="pageSizeOptions"
              :total="total"
              :showTotal="total => `共 ${total} 条`"
              showSizeChanger
              showQuickJumper
              :pageSize="pageSize"
              v-model="current"
              @showSizeChange="onShowSizeChange"
            >
              <template slot="buildOptionText" slot-scope="props">
                <span v-if="props.value!=='50'">{{props.value}}条/页</span>
                <span v-if="props.value==='50'">全部</span>
              </template>
            </a-pagination>
          </div>

          <!-- 修改企业弹出框 -->
          <a-modal class="motify-modal" title="修改企业信息" v-model="visibleMotify" @ok="handleOkMotify">
            <a-form :form="formMotify" @submit="handleSubmitMotify">
              <a-form-item label="企业名称：" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
                <a-input
                  class="motify-modal-input"
                  placeholder="请输入企业名称"
                  @blur.prevent="addchangeInput(0)"
                  v-decorator="[
                      'businessName',
                      {rules: [{ required: true, message: '企业名称为空' }]}
                    ]"
                />
              </a-form-item>

              <a-form-item label="备注：" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
                <a-textarea
                  @blur.prevent="addchangeTextarea(0)"
                  placeholder="请输入备注内容"
                  v-decorator="[
                      'note',
                      {rules: [{ required: true, message: '备注为空' }]}
                    ]"
                />
              </a-form-item>
            </a-form>
          </a-modal>

          <!-- 新增企业弹出框 -->
          <a-modal class="motify-modal" title="添加企业信息" v-model="visibleadd" @ok="handleOkadd">
            <a-form :form="formadd" @submit="handleSubmitadd">
              <a-form-item label="企业名称：" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
                <a-input
                  class="motify-modal-input"
                  placeholder="请输入企业名称"
                  @blur.prevent="addchangeInput(1)"
                  v-decorator="[
                      'businessName',
                      {rules: [{ required: true, message: '企业名称为空' }]}
                    ]"
                />
              </a-form-item>

              <a-form-item label="备注：" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
                <a-textarea
                  @blur.prevent="addchangeTextarea(1)"
                  placeholder="请输入备注内容"
                  v-decorator="[
                      'note',
                      {rules: [{ required: true, message: '备注为空' }]}
                    ]"
                />
              </a-form-item>
            </a-form>
          </a-modal>
        </a-layout>
      </div>
    </div>
  </div>
</template>


<script>
const columns = [
  {
    title: "企业库ID：",
    dataIndex: "businessId",
    key: "businessId",
    width: '10%',
    align: "center"
  },
  {
    title: "企业名称",
    dataIndex: "businessName",
    key: "businessName",
    width: '20%',
    align: "center",
    scopedSlots: { customRender: "businessName" }
  },
  {
    title: "备注",
    dataIndex: "note",
    key: "note",
    align: "center",
    width: "30%"
  },
  {
    title: "操作",
    width:'10%',
    key: "operation",
    align: "center",
    scopedSlots: { customRender: "action" }
  }
];

import { companyListdata, companyListUpdata ,companyListadd,companyListDelete} from "../api/ProductApi";

export default {
  data() {
    return {
      pageSizeOptions: ["10", "20", "30", "40", "50"],
      current: 1,
      pageSize: 10,
      pageNumber: 1,
      total: 0,
      keyword: "",
      searchid: "",
      searchname: "",
      columns,
      visibleMotify: false,
      visibleadd:false,
      showpagination: false,
      busid: 0,
      formMotify: this.$form.createForm(this),
      formadd: this.$form.createForm(this),
      dataEnterprise: []
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      companyListdata({page:this.pageNumber,pageSize: this.pageSize,businessId:this.searchid,businessName:this.searchname}).then(res => {
        // console.log(res.data.data);
        this.total = res.data.data.total
        this.dataEnterprise = res.data.data.list;
      });
    },
    // 右边的页码
    onChangepagination(pageNumber) {
      this.pageNumber = pageNumber;
      // console.log("Page: ", pageNumber);
      this.init();
    },
      onShowSizeChange(current, pageSize) {
      this.pageSize = pageSize;
      this.pageNumber = 1
      // console.log(this.pageSize);
      // console.log(current);
      this.init();
    },
    searchSubmit() {
      this.pageNumber = 1
      this.init()
    },
    showEditClassificationVisible(record) {
      this.visibleMotify = true;
      this.busid = record.businessId;
      // console.log(record);
      setTimeout(() => {
        //加了个定时器，避免了表单初始化的时候报错
        this.formMotify.setFieldsValue({
          businessName: record.businessName,
          note: record.note
        });
      }, 0);
      // console.log(this.formMotify);
    },
    handleOkMotify() {
      this.handleSubmitMotify();
    },
    handleSubmitMotify(e) {
      this.formMotify.validateFields((err, values) => {
        if (!err) {
           this.visibleMotify = false;
          console.log("Received values of form: ", {
            businessId: this.busid,
            businessName: values.businessName,
            note: values.note
          });
        let motifyForm ={
           businessId: this.busid,
            businessName: values.businessName,
            note: values.note
          }

        console.log(motifyForm);
          companyListUpdata(motifyForm).then(res => {
            if (res.data.code == this.$global.successCode) {
              this.$message.success("修改企业信息成功");
              this.init();
            }else{
               this.$message.warning(`${res.data.message}`)
            }
          });
        }
      });
    },
    addchangeInput(type) {
      // console.log('失去焦点报红');
      if(type==0){
         this.formMotify.validateFields(["businessName"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
      }
      if(type==1){
         this.formadd.validateFields(["businessName"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
      }
     
    },
    addchangeTextarea(type) {
      if(type ==0){
       this.formMotify.validateFields(["note"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
      }

        if(type==1){
         this.formadd.validateFields(["note"], (err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
        }
      });
      }
    },
    showAddClassificationVisible(){
      this.visibleadd = true
      this.formadd = this.$form.createForm(this)

    },
    handleOkadd(){
     this.handleSubmitadd()
    },
    handleSubmitadd(){
     this.formadd.validateFields((err, values) => {
        if (!err) {
           this.visibleadd = false
          console.log("Received values of form: ", {
            businessName: values.businessName,
            note:values.note
          });
          companyListadd({
            businessName: values.businessName,
            note: values.note
          }).then(res => {
            if (res.data.code == this.$global.successCode) {
              this.$message.success("新增企业信息成功");
              this.init();
            }
          });
        }
      });
    },
      //删除企业
    onDelete(businessId) {
      console.log(businessId);
      companyListDelete(businessId).then((res)=>{
        if (res.data.code === this.$global.successCode) {
           this.$message.success("删除企业成功!")
           this.init();
        }
      }).catch((err)=>{
        throw err
      })
     
    },
  }
};
</script>
